import React, { Component } from "react";
import { S } from "selector_dom"

export function wait(t) {
  return new Promise((e) => setTimeout(e, t));
}

export function onload() {
  window.addEventListener("keydown", (e) => {
    if (e.key === "Escape") {
      window.history.back();
    }
  });
  // diclaring varibles to use theme later
  const date = new Date();
  const [hour, darkMode, autoDarkMode] = [
    date.getHours(),
    window.localStorage.getItem("darkMode"),
    window.localStorage.getItem("autoDarkMode"),
  ];

  // check for localStorage variable
  if (!darkMode) window.localStorage.setItem("darkMode", false);
  if (!autoDarkMode) window.localStorage.setItem("autoDarkMode", false);
  if (!window.localStorage.getItem("tips")) window.localStorage.setItem("tips", true);
  if (!window.localStorage.getItem("theme")) window.localStorage.setItem("theme", JSON.stringify(["#099a97", "#15cda8"]));

  // setup theme colors
  const themeColors = JSON.parse(window.localStorage.getItem("theme"));
  document.documentElement.style.setProperty("--main-color", themeColors[0]);
  document.documentElement.style.setProperty("--second-color", themeColors[1]);
  document.querySelector("meta[name=theme-color]").setAttribute("content", themeColors[0]);

  // for darkMode
  if (
    (darkMode === "true" && autoDarkMode === "true" && (hour >= 20 || hour <= 6)) ||
    (darkMode === "true" && autoDarkMode === "false")
  ) {
    turnOnDarkMode();
  } else if (darkMode === "true") {
    var timer = setInterval(function () {
      const date = new Date();
      const getHour = date.getHours();
      if (!window.localStorage.getItem("autoDarkMode")) {
        clearInterval(timer);
      }
      if (getHour >= 20 || getHour <= 6) {
        turnOnDarkMode();
      }
    }, 60000);
  }

  // change viewPort css variable for font size
  if (window.innerWidth > 1000) document.documentElement.style.setProperty("--vw", "50vw");
  window.addEventListener("resize", () =>
    document.documentElement.style.setProperty("--vw", window.innerWidth > 1000 ? "50vw" : "100vw")
  );
}

function turnOnDarkMode() {
  document.documentElement.style.setProperty("--background-gray", "#18191a");
  document.documentElement.style.setProperty("--background-white", "#242526");
  document.documentElement.style.setProperty("--background-kinda-white", "#3a3b3c");
  document.documentElement.style.setProperty("--text-kinda-black", "#e4e6eb");
  document.documentElement.style.setProperty("--text-gray", "#a7aaae");
  document.documentElement.style.setProperty("--select-background-image", `url(${require("./assets/down-arrow-white.svg")})`);
}

export let toEnglishNumber = function (strNum) {
  var ar = "٠١٢٣٤٥٦٧٨٩".split("");
  var en = "0123456789".split("");
  strNum = strNum.replace(/[٠١٢٣٤٥٦٧٨٩]/g, (x) => en[ar.indexOf(x)]);
  strNum = strNum.replace(/(?![.])[\D]/g, "");
  return strNum;
};

