import React, { Component } from "react";
import "./Styles/Loading.css";
import { S } from "selector_dom";
import lottie from "lottie-web";
import lottieLoading from "./assets/lottieLoading.json";

let tl;
export default class Loading extends Component {

  componentDidMount() {
    this.loadingAnimation();
    lottie.loadAnimation({
      container: document.getElementById("loadingPageLottie"),
      renderer: "svg",
      loop: true,
      autoplay: true,
      animationData: lottieLoading,
    });
  };

  async componentDidUpdate() {
    if (this.props.end) {
      S("#fillPageColor").css({ transform: "translate(-50%, -50%) scale(221)" });
      await S().wait(300);
      clearInterval(tl);
      S("#loadingPage").css({ display: "none" });
      S("#loadingPage svg").remove();
    }
  };

  loadingAnimation = () => {
    const el = document.getElementById("laodingTxt");
    let counter = -1;
    tl = setInterval(() => {
      counter++;
      counter === 0
        ? (el.innerHTML = "جاري التحميل ")
        : counter === 1
        ? (el.innerHTML = "جاري التحميل .")
        : counter === 2
        ? (el.innerHTML = "جاري التحميل ..")
        : counter === 3
        ? (el.innerHTML = "جاري التحميل ...")
        : (counter = -1);
    }, 400);
  };

  render() {
    return (
      <div id="loadingPage" style={{ height: window.innerHeight + "px" }}>
        <div id="loadingPageLottie"></div>
        <p id="laodingTxt">جاري التحميل</p>
        <div id="fillPageColor"></div>
      </div>
    );
  }
}
